/********other mixins **********/
.html-registration-page .master-wrapper-page .master-wrapper-content {
  width: auto;
}
.html-registration-page .master-wrapper-page .master-wrapper-content .topic-block {
  position: relative;
}
.html-registration-page .master-wrapper-page .master-wrapper-content .topic-block .topic-block-title {
  display: none;
}
.html-registration-page .master-wrapper-page .master-wrapper-content .topic-block .topic-block-body p img {
  width: 100%;
  height: 456px;
}
.html-registration-page .center-1 {
  margin-bottom: 0px;
}
@media only screen and (min-width: 992px) {
  .html-registration-page .center-1 {
    margin-bottom: 50px;
  }
}
.html-registration-page .registration-page .page-title {
  margin-top: 32px;
}
@media only screen and (max-width: 575px) {
  .html-registration-page .registration-page .page-title {
    margin-top: 70px;
  }
}
.html-registration-page .registration-page .page-title h1 {
  font-size: 1.875rem;
  color: #444444;
  text-transform: capitalize;
}
@media only screen and (max-width: 575px) {
  .html-registration-page .registration-page .page-title h1 {
    font-size: 1.375rem;
  }
}
.html-registration-page .registration-page .fieldset {
  margin: 0px;
  border-left: 1px solid #eeeeee;
  border-right: 1px solid #eeeeee;
  border-radius: 3px;
}
.html-registration-page .registration-page .fieldset .title {
  margin: 0;
  padding: 18px;
  font-size: 1.125rem;
  color: #8c8c8c;
  background-color: #f9f9f9;
  border-radius: 3px 3px 0 0;
}
.html-registration-page .registration-page .fieldset .title strong {
  font-weight: 300;
  color: #8c8c8c;
  font-size: 1.125rem;
}
.html-registration-page .registration-page .fieldset:first-child .title {
  margin-top: 12px;
  border-top: 1px solid #eeeeee;
}
.html-registration-page .registration-page .password-block {
  border-bottom: 1px solid #eeeeee;
}
.html-registration-page .registration-page .form-fields {
  background: #ffffff;
  margin: 0px;
  border-top: none;
}
@media only screen and (min-width: 768px) {
  .html-registration-page .registration-page .form-fields {
    margin: 0px 50px;
  }
}
@media only screen and (min-width: 1200px) {
  .html-registration-page .registration-page .form-fields {
    margin: 0px 100px;
  }
}
.html-registration-page .registration-page .form-fields .inputs {
  margin: 13px 0 21px;
}
.html-registration-page .registration-page .form-fields .inputs .required {
  margin: 0px 0px 0 3px;
}
.html-registration-page .registration-page .form-fields .inputs .gender span {
  margin: 0 37px 0 0px;
}
@media only screen and (max-width: 575px) {
  .html-registration-page .registration-page .form-fields .inputs .gender span label {
    width: 60px;
  }
}
.html-registration-page .registration-page .form-fields .inputs input[type=text] {
  height: 45px;
  color: #8c8c8c;
  border-radius: 3px;
  width: 100%;
}
.html-registration-page .registration-page .form-fields .inputs input[type=checkbox] {
  opacity: 0;
  margin-bottom: 7px;
}
.html-registration-page .registration-page .form-fields .newsletter-section {
  column-gap: 10px;
  display: inline-flex;
}
.html-registration-page .registration-page .form-fields .newsletter-section .newsletter-text {
  color: #444444;
  font-weight: 300;
}
.html-registration-page .registration-page .form-fields .newsletter-section label {
  display: flex;
  align-items: center;
}
.html-registration-page .registration-page .form-fields .date-of-birth select {
  max-width: 33.5%;
  height: 45px;
  color: #8c8c8c;
  border-radius: 3px;
  width: 100%;
  padding: 0px;
  padding-left: 12px;
}
.html-registration-page .registration-page .form-fields .date-of-birth select + select {
  margin: 0px;
  margin-left: -1px;
  border-radius: 0 3px 3px 0;
}
.html-registration-page .registration-page .buttons {
  margin: 0 0 48px;
}
.html-registration-page .registration-page .external-authentication {
  margin-bottom: 0px;
}